




























  export default {
		data(){
			return{
				tabLists: [
          {
            name: '办卡问题'
          },
          {
            name: 'VIP问题'
          }
        ],
        tabIndex: 0,
        loading: false,
        lists: []
			}
		},
    onLoad() {
    	this.updateLists(this.tabIndex)
    },
		methods:{
			gotoFeedback() {
        this.$navigateTo({
        	url: '/pages/mine/feedback'
        })
      },
      gotoNotes() {
        this.$navigateTo({
        	url: '/pages/mine/notes'
        })
      },
      updateLists(n) {
        if (this.loading) return
        this.loading = true
        this.$http({
          url: this.$apis.help,
          method: 'GET',
          data: {
            type: n + 1
          },
          cb: (err, data) => {
            if (!err && data.success === true) {
              this.lists = data.data.data
              this.lists.forEach(item => {
                this.$set(item, 'show', false)
              })
              setTimeout(() => {
                this.loading = false
              }, 600)
            } else {
              this.loading = false
              uni.showToast({
              	title: '数据获取失败',
                icon: 'none'
              })
            }
          }
        })
      },
      tabClick(n) {
        if (this.tabIndex === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.lists = []
        this.tabIndex = n
        this.updateLists(this.tabIndex)
      },
      slideToggle(that) {
        this.lists.forEach((item, i) => {
          if (item.id === that.id) {
            item.show = !item.show
          } else {
            item.show = false
          }
        })
      }
		}
	}
